<template>
  <van-pull-refresh v-model="isLoading" @refresh="getTopic">
    <div :class="$style.waper">
      <v-topic v-for="(item, index) in topics" :key="index" :topicInfo="item"></v-topic>
      <v-bottom-line/>
    </div>
  </van-pull-refresh>
</template>

<script>
import { PullRefresh } from 'vant'
import Topic from '@/components/Topic'
import { TABS } from '../../config'
import BottomLine from '@/base/BottomLine'
import { fetchTopics } from '../../util/api'
export default {
  name: 'x-topics',
  components: {
    [PullRefresh.name]: PullRefresh,
    [BottomLine.name]: BottomLine,
    [Topic.name]: Topic
  },
  props: {
    ID: {
      validator (v) {
        return TABS[v]
      },
      required: true
    }
  },
  data () {
    return {
      topics: [],
      isLoading: false,
      page: 1
    }
  },
  methods: {
    async getTopic () {
      try {
        const res = await fetchTopics({page: this.page, tab: TABS[this.ID]})
        if (res.success) {
          this.topics.unshift(...res.data)
          this.page += 1
        }
        if (this.topics.length >= 50) {
          this.topics.splice(-10)
        }
        setTimeout(() => {
          this.isLoading = false
        }, 500)
      } catch (error) {
        this.isLoading = false
        console.error(error)
      }
    }
  },
  async created () {
    this.$toast.loading({
      mask: true,
      message: '加载中...'
    })
    await this.getTopic()
    this.$toast.clear()
  }
}
</script>

<style module>
.waper{
  background-color: #eeeeee;
}
</style>



