<template>
  <v-header title="分类" :class="$style.mixin" @click-left="$router.go(-1)">
    <i class="iconfont icon-back" :class="$style.back" slot="left"></i>
    <i class="iconfont icon-more" :class="$style.back" slot="right"></i>
  </v-header>
</template>

<script>
import Header from '@/base/Header'
export default {
  name: 'x-header',
  data () {
    return {
    }
  },
  components: {
    [Header.name]: Header
  },
  methods: {
  }
}
</script>

<style module>
.back{
  color: #3388ff;
  font-weight: 100;
  font-size: 20px;
}
.mixin{
  font-size: 16px;
  font-weight: normal;
}
</style>



