<template>
  <van-tabs v-model="active" sticky>
    <van-tab v-for="(item, index) in tabs" :key="index" :title="item">
      <x-topics :ID="index"></x-topics>
    </van-tab>
  </van-tabs>
</template>

<script>
import { Tab, Tabs } from 'vant'
import Topics from './Topics'
import { TAB_NAME } from '../../config'
export default {
  name: 'x-tab',
  components: {
    [Tab.name]: Tab,
    [Topics.name]: Topics,
    [Tabs.name]: Tabs
  },
  data () {
    return {
      active: 3,
      tabs: TAB_NAME
    }
  }
}
</script>

<style>
</style>



